"use client";

import Link from "next/link";
import { useState } from "react";
import { Button } from "@/components/ui/button";
import ProfileDropdown from "@/components/ProfileDropdown";

interface HeaderProps {
  onAuthClick: () => void;
}

export default function Header({ onAuthClick }: HeaderProps) {
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  return (
    <>
      <header className="py-4 px-6 border-b border-border bg-background/95 backdrop-blur supports-[backdrop-filter]:bg-background/60 sticky top-0 z-50">
        <nav className="max-w-7xl mx-auto flex items-center justify-between" aria-label="Main navigation">
          <div className="text-2xl font-bold text-primary">
            <Link href="/" className="hover:opacity-80 transition-opacity">
              LuxeBrand
            </Link>
          </div>
          
          {/* Desktop Navigation */}
          <ul className="hidden md:flex items-center space-x-8">
            <li>
              <Link href="/" className="text-foreground hover:text-primary transition-colors font-medium">
                Home
              </Link>
            </li>
            <li>
              <Link href="#categories" className="text-foreground hover:text-primary transition-colors font-medium">
                Products
              </Link>
            </li>
            <li>
              <Link href="#about" className="text-foreground hover:text-primary transition-colors font-medium">
                About
              </Link>
            </li>
            <li>
              <Link href="#contact" className="text-foreground hover:text-primary transition-colors font-medium">
                Contact
              </Link>
            </li>
            <li>
              <ProfileDropdown onAuthClick={onAuthClick} />
            </li>
          </ul>

          {/* Mobile Menu Button */}
          <button
            className="md:hidden p-2 rounded-md hover:bg-accent"
            onClick={() => setIsMenuOpen(!isMenuOpen)}
            aria-label="Toggle menu"
          >
            <div className="w-6 h-6 flex flex-col justify-center space-y-1">
              <span className={`block h-0.5 w-6 bg-foreground transition-transform ${isMenuOpen ? 'rotate-45 translate-y-1.5' : ''}`}></span>
              <span className={`block h-0.5 w-6 bg-foreground transition-opacity ${isMenuOpen ? 'opacity-0' : ''}`}></span>
              <span className={`block h-0.5 w-6 bg-foreground transition-transform ${isMenuOpen ? '-rotate-45 -translate-y-1.5' : ''}`}></span>
            </div>
          </button>

          {/* Mobile Navigation */}
          {isMenuOpen && (
            <div className="absolute top-full left-0 right-0 bg-background border-b border-border md:hidden">
              <ul className="flex flex-col space-y-4 p-6">
                <li>
                  <Link href="/" className="text-foreground hover:text-primary transition-colors font-medium block" onClick={() => setIsMenuOpen(false)}>
                    Home
                  </Link>
                </li>
                <li>
                  <Link href="#categories" className="text-foreground hover:text-primary transition-colors font-medium block" onClick={() => setIsMenuOpen(false)}>
                    Products
                  </Link>
                </li>
                <li>
                  <Link href="#about" className="text-foreground hover:text-primary transition-colors font-medium block" onClick={() => setIsMenuOpen(false)}>
                    About
                  </Link>
                </li>
                <li>
                  <Link href="#contact" className="text-foreground hover:text-primary transition-colors font-medium block" onClick={() => setIsMenuOpen(false)}>
                    Contact
                  </Link>
                </li>
                <li className="pt-4 border-t border-border">
                  <Button 
                    variant="ghost" 
                    className="w-full text-left"
                    onClick={() => {
                      onAuthClick();
                      setIsMenuOpen(false);
                    }}
                  >
                    Login
                  </Button>
                </li>
                <li>
                  <Button 
                    className="w-full bg-gradient-to-r from-yellow-400 to-orange-500 text-black hover:from-yellow-500 hover:to-orange-600"
                    onClick={() => {
                      onAuthClick();
                      setIsMenuOpen(false);
                    }}
                  >
                    Register
                  </Button>
                </li>
              </ul>
            </div>
          )}
        </nav>
      </header>
    </>
  );
}
