"use client";

import { useState, useRef, useEffect } from "react";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Button } from "@/components/ui/button";
import { Separator } from "@/components/ui/separator";
import { ChevronDown, User, ShoppingBag, Heart, Phone, MapPin, Settings, LogOut } from "lucide-react";
import Link from "next/link";

interface ProfileDropdownProps {
  onAuthClick: () => void;
}

export default function ProfileDropdown({ onAuthClick }: ProfileDropdownProps) {
  const [isOpen, setIsOpen] = useState(false);
  const dropdownRef = useRef<HTMLDivElement>(null);

  // Mock user data - replace with actual user data
  const user = {
    name: "Anurag",
    phone: "6398332065",
    isLoggedIn: true
  };

  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (dropdownRef.current && !dropdownRef.current.contains(event.target as Node)) {
        setIsOpen(false);
      }
    };

    document.addEventListener("mousedown", handleClickOutside);
    return () => document.removeEventListener("mousedown", handleClickOutside);
  }, []);

  const menuItems = [
    { label: "Orders", icon: ShoppingBag, href: "/profile?tab=orders" },
    { label: "Wishlist", icon: Heart, href: "/profile?tab=wishlist" },
    { label: "Contact Us", icon: Phone, href: "/contact" },
    { label: "Saved Addresses", icon: MapPin, href: "/profile?tab=addresses" },
    { label: "Edit Profile", icon: Settings, href: "/profile?tab=profile" },
  ];

  if (!user.isLoggedIn) {
    return (
      <Button 
        onClick={onAuthClick}
        className="bg-gradient-to-r from-yellow-400 to-orange-500 text-black hover:from-yellow-500 hover:to-orange-600"
      >
        Login
      </Button>
    );
  }

  return (
    <div className="relative" ref={dropdownRef}>
      <Button
        variant="ghost"
        className="flex items-center space-x-2 hover:bg-accent/50"
        onClick={() => setIsOpen(!isOpen)}
      >
        <Avatar className="w-8 h-8">
          <AvatarImage src="/api/placeholder/32/32" alt={user.name} />
          <AvatarFallback className="text-xs bg-gradient-to-r from-yellow-400 to-orange-500 text-black">
            {user.name.charAt(0)}
          </AvatarFallback>
        </Avatar>
        <ChevronDown className={`w-4 h-4 transition-transform ${isOpen ? 'rotate-180' : ''}`} />
      </Button>

      {isOpen && (
        <div className="absolute right-0 mt-2 w-64 bg-white dark:bg-slate-800 rounded-lg shadow-lg border border-border z-50">
          {/* User Info Header */}
          <div className="p-4 border-b border-border">
            <div className="flex items-center space-x-3">
              <Avatar className="w-10 h-10">
                <AvatarImage src="/api/placeholder/40/40" alt={user.name} />
                <AvatarFallback className="bg-gradient-to-r from-yellow-400 to-orange-500 text-black">
                  {user.name.charAt(0)}
                </AvatarFallback>
              </Avatar>
              <div>
                <p className="font-semibold text-sm">Hello {user.name}</p>
                <p className="text-xs text-muted-foreground">{user.phone}</p>
              </div>
            </div>
          </div>

          {/* Menu Items */}
          <div className="py-2">
            {menuItems.map((item, index) => (
              <Link
                key={index}
                href={item.href}
                className="flex items-center space-x-3 px-4 py-2.5 text-sm hover:bg-accent/50 transition-colors"
                onClick={() => setIsOpen(false)}
              >
                <item.icon className="w-4 h-4 text-muted-foreground" />
                <span>{item.label}</span>
              </Link>
            ))}
          </div>

          <Separator />

          {/* Logout Button */}
          <div className="p-2">
            <Button
              variant="ghost"
              className="w-full flex items-center space-x-3 justify-start px-4 py-2.5 text-sm hover:bg-accent/50 text-red-600 hover:text-red-700"
              onClick={() => {
                setIsOpen(false);
                // Handle logout logic here
              }}
            >
              <LogOut className="w-4 h-4" />
              <span>Logout</span>
            </Button>
          </div>
        </div>
      )}
    </div>
  );
}
