"use client";

import Link from "next/link";
import Image from "next/image";
import { Button } from "@/components/ui/button";
import { useState, useEffect } from "react";

const backgroundImages = [
  {
    src: "https://images.pexels.com/photos/291762/pexels-photo-291762.jpeg",
    alt: "Woman wearing elegant jewelry and dress"
  },
  {
    src: "https://images.pexels.com/photos/937481/pexels-photo-937481.jpeg",
    alt: "Man in stylish suit and accessories"
  },
  {
    src: "https://images.pexels.com/photos/1191710/pexels-photo-1191710.jpeg",
    alt: "Woman wearing beautiful jewelry"
  },
  {
    src: "https://images.pexels.com/photos/842811/pexels-photo-842811.jpeg",
    alt: "Fashionable man in trendy outfit"
  },
  {
    src: "https://images.pexels.com/photos/247298/pexels-photo-247298.jpeg",
    alt: "Elegant woman with jewelry and fashion"
  }
];

export default function HeroSection() {
  const [currentImageIndex, setCurrentImageIndex] = useState(0);

  useEffect(() => {
    const interval = setInterval(() => {
      setCurrentImageIndex((prevIndex) => 
        (prevIndex + 1) % backgroundImages.length
      );
    }, 4000); // Change image every 4 seconds

    return () => clearInterval(interval);
  }, []);

  return (
    <section className="relative min-h-[80vh] flex items-center justify-center text-center overflow-hidden">
      {/* Background Image Carousel */}
      <div className="absolute inset-0">
        {backgroundImages.map((image, index) => (
          <div
            key={index}
            className={`absolute inset-0 transition-opacity duration-1000 ${
              index === currentImageIndex ? 'opacity-100' : 'opacity-0'
            }`}
          >
            <Image
              src={image.src}
              alt={image.alt}
              fill
              className="object-cover"
              style={{ objectPosition: 'center 20%' }}
              priority={index === 0}
              sizes="100vw"
            />
          </div>
        ))}
      </div>

      {/* Gradient overlay for better text readability */}
      <div className="absolute inset-0 bg-gradient-to-b from-black/30 via-black/50 to-black/70"></div>
      
      {/* Content */}
      <div className="relative z-10 max-w-4xl mx-auto px-6 py-20">
        <h1 className="text-5xl md:text-7xl font-bold text-white mb-6 leading-tight">
          <span className="bg-gradient-to-r from-white to-yellow-200 bg-clip-text text-transparent">
            Discover Your
          </span>
          <br />
          <span className="bg-gradient-to-r from-yellow-200 via-white to-yellow-100 bg-clip-text text-transparent">
            Perfect Style
          </span>
        </h1>
        
        <p className="text-xl md:text-2xl text-yellow-100 mb-8 max-w-2xl mx-auto leading-relaxed font-light">
          From exquisite <span className="text-yellow-200 font-semibold">anti-tarnish jewelry</span> to 
          <span className="text-yellow-200 font-semibold"> premium clothing</span> for men and women, 
          find everything you need to express your unique style.
        </p>
        
        <div className="flex justify-center items-center">
          <Button asChild size="lg" className="text-lg px-10 py-6 bg-gradient-to-r from-yellow-400 to-orange-500 text-black hover:from-yellow-500 hover:to-orange-600 shadow-lg hover:shadow-xl transition-all duration-300">
            <Link href="#categories">
              Explore Collections
            </Link>
          </Button>
        </div>
        
        <div className="mt-16 grid grid-cols-1 md:grid-cols-3 gap-8 text-center">
          <div className="p-6 bg-gradient-to-br from-yellow-400/20 to-orange-400/20 backdrop-blur-sm rounded-lg border border-yellow-200/30">
            <div className="text-3xl font-bold text-yellow-200 mb-2">1000+</div>
            <div className="text-yellow-100">Premium Products</div>
          </div>
          <div className="p-6 bg-gradient-to-br from-orange-400/20 to-red-400/20 backdrop-blur-sm rounded-lg border border-orange-200/30">
            <div className="text-3xl font-bold text-orange-200 mb-2">50K+</div>
            <div className="text-orange-100">Happy Customers</div>
          </div>
          <div className="p-6 bg-gradient-to-br from-red-400/20 to-pink-400/20 backdrop-blur-sm rounded-lg border border-red-200/30">
            <div className="text-3xl font-bold text-red-200 mb-2">24/7</div>
            <div className="text-red-100">Customer Support</div>
          </div>
        </div>
      </div>

      {/* Image indicators */}
      <div className="absolute bottom-8 left-1/2 transform -translate-x-1/2 flex space-x-2 z-10">
        {backgroundImages.map((_, index) => (
          <button
            key={index}
            onClick={() => setCurrentImageIndex(index)}
            className={`w-3 h-3 rounded-full transition-all duration-300 ${
              index === currentImageIndex 
                ? 'bg-white scale-110' 
                : 'bg-white/50 hover:bg-white/70'
            }`}
            aria-label={`Go to slide ${index + 1}`}
          />
        ))}
      </div>

      {/* Decorative elements - now more subtle */}
      <div className="absolute top-20 left-10 w-20 h-20 bg-white/10 rounded-full blur-xl"></div>
      <div className="absolute bottom-20 right-10 w-32 h-32 bg-white/10 rounded-full blur-xl"></div>
      <div className="absolute top-1/2 left-1/4 w-16 h-16 bg-white/10 rounded-full blur-lg"></div>
    </section>
  );
}
